import { useStaticQuery, graphql } from 'gatsby';
import { FluidObject } from 'gatsby-image';
import { Post } from 'models/post';

type AboutImagesKey = 'me' | 'simpleAnn' | 'simpleWaveform' | 'solarSystem' | 'vueWithCoffee' | 'zarbis';
type SocialKey = 'github' | 'linkedin' | 'rss';

export interface SimplePostGroupResult {
  fieldValue: string;
  nodes: Post[];
}

type AboutSiteQueryResult = {
  site: {
    siteMetadata: {
      author: {
        email: string;
      };
      social: {
        [key in SocialKey]: string;
      };
    };
  };
};
type AboutContentsQueryResult = {
  allMarkdownRemark: {
    group: SimplePostGroupResult[];
  };
};
type AboutImagesQueryResult = {
  [key in AboutImagesKey]: {
    childImageSharp: {
      fluid: FluidObject;
    };
  };
};

export const useAboutData = () => {
  const queryResult: AboutImagesQueryResult & AboutSiteQueryResult & AboutContentsQueryResult = useStaticQuery(graphql`
    query AboutImagesQuery {
      site {
        siteMetadata {
          author {
            email
          }
          social {
            github
            linkedin
            rss
          }
        }
      }
      allMarkdownRemark {
        group(field: frontmatter___relatedProject) {
          fieldValue
          nodes {
            fields {
              path
            }
            frontmatter {
              title
              date
              categories
              thumbnail {
                childImageSharp {
                  fixed(width: 320, height: 320, quality: 100, cropFocus: CENTER) {
                    src
                  }
                }
              }
            }
          }
        }
      }
      me: file(absolutePath: { regex: "/me.jpg/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      simpleAnn: file(absolutePath: { regex: "/simple-ann.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      simpleWaveform: file(absolutePath: { regex: "/simple-waveform.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      solarSystem: file(absolutePath: { regex: "/solar-system.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      vueWithCoffee: file(absolutePath: { regex: "/vue-with-coffee/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      zarbis: file(absolutePath: { regex: "/zarbis/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return queryResult;
};
